import { mapMutations, mapGetters } from 'vuex';
import del from '@/static/mbcImg/my/del.png'
export default {
	data() {
		return {
			list:[],
			del:del,
			hiden:2,
			num:0,
			edit:'',
			blurType: '1', // 代表默认失去焦点事件，2代表点击添加时失去焦点事件
		};
	},
	components: {

	},
	computed: {
		i18n() {
			return this.$t('Mypersonal');
		},
		...mapGetters(['GET_HOME', 'QUICKNAVCO','GET_MY'])
	},
	onLoad() {
		uni.setNavigationBarTitle({
			title: this.i18n.title1
		});
	},
	created() {
		this.Keyword();
	},
	methods: {
		Keyword(){
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				// let params = {}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/user/760', //接口地址。
					// data: params,
					method: 'GET',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.list = response.data.content.purcKeys
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
		dele(e){
			console.log(e)
			this.list.splice(e,1)
			console.log(this.list)
		},
		Preservation(){
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				if(this.list.length===0){
					this.list.push('')
					var params = {
						"purcKeys":this.list,
					}; // 请求总数居时 参数为空
				}else{
					var params = {
						"purcKeys":this.list,
					}; // 请求总数居时 参数为空
				}
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/user/760', //接口地址。
					data: params,
					method: 'POST',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.GET_MY.headers.purcKeys = this.list;
						this.$store.commit('setMy', this.GET_MY.headers); // 更新vuex
						uni.navigateBack({});
						// this.$store.commit('setMation', this.List); // 更新vuex
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
		add(){
			this.hiden = '1';
			this.num = '1';
			console.log(this.list)
		},
		
		sub(){
			if(this.edit === ''){ // 失脚时没有数据
				this.hiden = 2
			}else{ // 失焦时存在数据
				this.hiden = 2;
				console.log('创建新标签');
				
				this.list.push(this.edit);
				this.edit = '';
			}
		},
	}
};
